// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name.charAt(0) || 'T'}
    />
  )
}

export const columns = [
  {
    name: 'Name',
    sortable: true,
    minWidth: '138px',
    sortField: 'uniqueidentity',
    selector: row => row.id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <Link
            to={`/task/view/${row.id}`}
            className='user_name text-truncate text-body'>
            <span className='fw-bolder'>{row.name}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'No Of Completed Total',
    sortable: true,
    minWidth: '172px',
    sortField: 'clientname',
    selector: row => row.completedtasks,
    cell: row => <span className='text-capitalize'>{row.completedtasks}</span>
  },
  {
    name: 'No Of Tasks Pending',
    sortable: true,
    minWidth: '172px',
    sortField: 'clientname',
    selector: row => row.todotasks,
    cell: row => <span className='text-capitalize'>{row.todotasks}</span>
  },
  {
    name: 'No Of Total Tasks',
    sortable: true,
    minWidth: '172px',
    sortField: 'clientname',
    selector: row => row.totaltasks,
    cell: row => <span className='text-capitalize'>{row.totaltasks}</span>
  }
]
